import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import MenuItem from "../../components/menuitem.js"

import { Row, Col } from "react-materialize"

export const menuImage = graphql`
  fragment menuImage on File {
    childImageSharp {
      fluid(maxWidth: 360, maxHeight: 240) {
        ...GatsbyImageSharpFluid
        ...GatsbyImageSharpFluidLimitPresentationSize
      }
    }
  }
`

const Noodle_Menu_Page = () => {
  const images = useStaticQuery(graphql`
    query {
      cold_soba: file(relativePath: { eq: "noodles/soba/cold_soba.png" }) {
        ...menuImage
      }
      tempura_soba: file(
        relativePath: { eq: "noodles/soba/tempura_soba.png" }
      ) {
        ...menuImage
      }
      beef_soba: file(relativePath: { eq: "noodles/soba/beef_soba.png" }) {
        ...menuImage
      }
      zaru_soba: file(relativePath: { eq: "noodles/soba/zaru_soba.png" }) {
        ...menuImage
      }
      curry_udon: file(relativePath: { eq: "noodles/udon/curry_udon.png" }) {
        ...menuImage
      }
      cold_udon: file(relativePath: { eq: "noodles/udon/cold_udon.png" }) {
        ...menuImage
      }
      beef_udon: file(relativePath: { eq: "noodles/udon/beef_udon.png" }) {
        ...menuImage
      }
      tempura_udon: file(
        relativePath: { eq: "noodles/udon/tempura_udon.png" }
      ) {
        ...menuImage
      }
      super_spicy_ramen: file(
        relativePath: { eq: "noodles/ramen/super_spicy_ramen.png" }
      ) {
        ...menuImage
      }
      kaisen_ramen: file(
        relativePath: { eq: "noodles/ramen/kaisen_ramen.png" }
      ) {
        ...menuImage
      }
      tsukemen: file(relativePath: { eq: "noodles/ramen/tsukemen.png" }) {
        ...menuImage
      }
      chilled_chinese_noodles: file(
        relativePath: { eq: "noodles/ramen/chilled_chinese_noodles.png" }
      ) {
        ...menuImage
      }
      shio_ramen: file(relativePath: { eq: "noodles/ramen/shio_ramen.png" }) {
        ...menuImage
      }
      shoyu_ramen: file(relativePath: { eq: "noodles/ramen/shoyu_ramen.png" }) {
        ...menuImage
      }
      tonkotsu_ramen: file(
        relativePath: { eq: "noodles/ramen/tonkotsu_ramen.png" }
      ) {
        ...menuImage
      }
      miso_ramen: file(relativePath: { eq: "noodles/ramen/miso_ramen.png" }) {
        ...menuImage
      }
      tomato_ramen: file(
        relativePath: { eq: "noodles/ramen/tomato_ramen.png" }
      ) {
        ...menuImage
      }
    }
  `)

  const [numTotalItems, setItems] = useState(0)

  const addItem = () => {
    setItems(numTotalItems + 1)
  }

  const removeItem = () => {
    setItems(numTotalItems - 1)
  }

  return (
    <Layout>
      <SEO title="Noodles | Online Ordering" />
      <div style={{ marginTop: "4%", marginLeft: "10%", width: "80%" }}>
        <Row>
          <Col s={3}>
            <MenuItem
              key={"1"}
              title="Cold Soba (Cold)"
              price={13.5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"2"}
              title="Zaru-Soba (Cold)"
              price={13}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.zaru_soba}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"3"}
              title="Tempura Soba (Hot or Cold)"
              price={16}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tempura_soba}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"4"}
              title="Beef Soba (Hot or Cold)"
              price={16}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.beef_soba}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"5"}
              title="Cold Udon (Cold)"
              price={12}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_udon}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"6"}
              title="Curry Udon (Hot)"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.curry_udon}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"7"}
              title="Tempura Udon (Hot or Cold)"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tempura_udon}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"8"}
              title="Beef Udon (Hot or Cold)"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.beef_udon}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"9"}
              title="Tempura Inaniwa Udon (Hot)"
              price={25}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tempura_udon}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"10"}
              title="Beef Inaniwa Udon(Hot)"
              price={25}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.beef_udon}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"16"}
              title="Shoyu Ramen"
              price={12.5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.shoyu_ramen}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"15"}
              title="Shio Ramen"
              price={12.5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.shio_ramen}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"11"}
              title="Super Spicy Ramen"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.super_spicy_ramen}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"12"}
              title="Kaisen Ramen"
              price={18}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.kaisen_ramen}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"14"}
              title="Tsukemen"
              price={18}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tsukemen}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"13"}
              title="Chilled Chinese Noodles"
              price={13}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.chilled_chinese_noodles}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"15"}
              title="Tomato Ramen"
              price={14.5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tomato_ramen}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"16"}
              title="Tonkotsu Ramen"
              price={13}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tonkotsu_ramen}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"17"}
              title="Miso Ramen"
              price={13.5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.miso_ramen}
            />
          </Col>
        </Row>
      </div>
    </Layout>
  )
}

export default Noodle_Menu_Page
